import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import accountSelect from '@/components/common-components/accountSelect';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        // tab切换配置
        this.selectedLabelDefault = 'unprocessed';
        this.tabBarList = [{
                label: '未完成',
                value: 'unprocessed'
            }, {
                label: '已完成',
                value: 'processed'
            }];
        this.openid = '';
        this.page = 1;
        this.orderList = [];
        this.isShowMore = false;
        this.isShowNoMore = false;
    }
    // 计算属性
    get formatMonth() {
        return this.format(this.selectDate, 'yyyy-MM');
    }
    activated() {
        this.orderList = [{ "_id": { "$oid": "5f797fdb0575942037b14d9e" }, "fee": 0.02, "body": "集合订单", "out_trade_no": "20201004155507007", "userId": "oJ-T_0irr1zac2cWKuSmRDp-xWWM", "commodityList": [{ "id": "0889c5c0-fb00-11ea-a4a8-297c219632b8", "name": "小油菜", "num": 1, "price": "0.01", "imgUrl": "http://img.beijixiong.club:4869/23f2d02edf274e395931b307fa2078e6" }, { "id": "14067010-0244-11eb-a74a-f3981c383190", "name": "哈密瓜", "num": 1, "price": "0.01", "imgUrl": "http://img.beijixiong.club:4869/2a4d2566daa8c8bd5a80ce22eafbb447" }, { "id": "0889c5c0-fb00-11ea-a4a8-297c219632b8", "name": "小油菜", "num": 1, "price": "0.01", "imgUrl": "http://img.beijixiong.club:4869/23f2d02edf274e395931b307fa2078e6" }], "businessId": "8a146790-f97e-11ea-8165-3111824eebe8", "type": "commodity", "payTime": 1601798107217, "payState": true, "time_end": "20201004155511" }];
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.pageInit();
    }
    pageInit() {
        let query = this.$route.query;
        if (query.openid) {
            this.openid = query.openid;
            this.getOrderList();
        }
        else if (query.code) {
            this.getUserInfo();
        }
    }
    tabBarChange(type) {
        this.page = 1;
        this.orderList = [];
        this.selectedLabelDefault = type;
        this.getOrderList();
    }
    getOrderList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getOrderList', { openid: this.openid, page: this.page, type: this.selectedLabelDefault }).then((response) => {
            if (response.success) {
                let resData = response.data;
                if (response.data.list.length >= 0 && response.data.list.length < 10) {
                    this.orderList = this.orderList.concat(resData.list);
                    this.isShowMore = false;
                }
                else if (response.data.list.length === 10) {
                    this.orderList = this.orderList.concat(resData.list);
                    this.isShowMore = true;
                }
                else {
                    this.isShowMore = false;
                }
                if (this.page === 1 && response.data.list.length === 0) {
                    this.isShowNoMore = true;
                }
            }
            toast.hide();
        });
    }
    getUserInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.getOrderList();
                        if (response.data.userInfo) {
                            this.userInfo = response.data.userInfo;
                        }
                        // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    commodityClick(item) {
        console.log(item);
        this.$createDialog({
            type: 'alert',
            confirmBtn: {
                text: '关闭',
                active: true
            }
        }, (createElement) => {
            return [
                createElement('div', {
                    'class': {
                        'my-title': true
                    },
                    slot: 'title'
                }, [
                    createElement('img', {
                        attrs: {
                            src: item.imgUrl + '?w=200'
                        }
                    }),
                    createElement('p', item.name)
                ]),
                createElement('p', {
                    'class': {
                        'my-content': true
                    },
                    slot: 'content'
                }, '价格:' + item.price + '  数量:' + item.num)
            ];
        }).show();
    }
    showMoreClick() {
        this.page++;
        this.getOrderList();
    }
    format(date, format) {
        //eg:format="yyyy-MM-dd hh:mm:ss";
        if (!format) {
            format = 'yyyy-MM-dd hh:mm:ss';
        }
        var o = {
            'M+': date.getMonth() + 1,
            'd+': date.getDate(),
            'H+': date.getHours(),
            'h+': date.getHours(),
            'm+': date.getMinutes(),
            's+': date.getSeconds(),
            'q+': Math.floor((date.getMonth() + 3) / 3),
            'S': date.getMilliseconds()
        };
        if (/(y+)/.test(format)) {
            format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (var k in o) {
            if (new RegExp('(' + k + ')').test(format)) {
                format = format.replace(RegExp.$1, RegExp.$1.length === 1
                    ? o[k]
                    : ('00' + o[k]).substr(('' + o[k]).length));
            }
        }
        return format;
    }
};
payAdmin = __decorate([
    Component({
        components: {
            accountSelect
        },
        filters: {
            getFormatDate(date) {
                if (date) {
                    return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
                }
                else {
                    return '';
                }
            },
            getImage(item) {
                if (item.commodity_info[0] && item.commodity_info[0].imgUrlList) {
                    return item.commodity_info[0].imgUrlList[0];
                }
                else {
                    return '';
                }
            },
            getType(item) {
                if (item.state === 'processed') {
                    return '已完成';
                }
                else {
                    return '未完成';
                }
            }
        }
    })
], payAdmin);
export default payAdmin;
